.dropdowns {
  position: relative;

  &__label {
    @extend %small-text;
    color: $c-secondary;
    margin-bottom: 4px;
    position: relative;
  }

  &__select {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @extend %buttons;
    padding: 10px;
    height: 40px;
    background-color: transparent;
    border: 1px solid $c-icons;
    outline: none;
    color: $c-primary;
    width: 100%;
    text-transform: capitalize;
    transition: 0.3s;

    &:hover {
      border: 1px solid $c-primary;
    }
  }

  &__arrow {
    @extend %flex-center;
    transition: transform 0.3s;

    &--open {
      transform: rotate(180deg);
    }
  }

  &__list-option {
    position: absolute;
    top: 105%;
    width: 100%;
    border: 1px solid $c-icons;
  }

  &__option {
    display: flex;
    align-items: center;
    padding: 5px;
    @extend %small-text;
    cursor: pointer;
    background-color: $c-white;
    height: 30px;
    transition: all 0.3s;
    border-bottom: 1px solid transparent;
  }

  &__option a {
    color: $c-secondary;
    width: 100%;
    height: 100%;
    text-transform: capitalize;

    &:hover {
      color: $c-primary;
      border-bottom: 1px solid $c-icons;
    }
  }
}
